

import plantumlEncoder from 'plantuml-encoder';
import axios from 'axios';
import lodash from 'lodash';
var _ = lodash;

var state = {
  official: 'http://plantuml.com/',
  plantuml: 'plantuml',
  server: 'https://plantuml-server.herokuapp.com/',
  defaultText: '@startuml\n\nactor User\n\nUser -right-> (select template)\nUser -down-> (write uml diagram)\n\n@enduml',
  editor: null,
  text: '',
  src: '',
  umlWidth: 50,
  umlExtension: 'svg',
  umlExtensions: [{
    'text': 'svg',
    'value': 'svg',
    'fileType': 'image/svg+xml',
    'responseType': 'text'
  }, {
    'text': 'png',
    'value': 'png',
    'fileType': 'image/png',
    'responseType': 'arraybuffer'
  }],
  isLoading: false,
  colSize: {
    'history': 2,
    'editor': 4,
    'uml': 6
  },
  renderUMLKey: {
    'win': 'Ctrl-Enter',
    'mac': 'Command-Enter'
  }
};

var mutations = {
  getUmlWidthFromLocalStorage: function getUmlWidthFromLocalStorage() {
    if (window.localStorage && window.localStorage.getItem('umlWidth')) {
      state.umlWidth = window.localStorage.getItem('umlWidth');
    }
  },
  setUmlWidth: function setUmlWidth(state, umlWidth) {
    state.umlWidth = umlWidth;
    if (window.localStorage) {
      window.localStorage.setItem('umlWidth', state.umlWidth);
    }
  },
  setUmlExtension: function setUmlExtension(state, umlExtension) {
    state.umlExtension = umlExtension;
  },
  setColSize: function setColSize(state, colSize) {
    if (colSize.history || colSize.history === 0) {
      state.colSize.history = colSize.history;
    }
    if (colSize.editor || colSize.editor === 0) {
      state.colSize.editor = colSize.editor;
    }
    if (colSize.uml || colSize.uml === 0) {
      state.colSize.uml = colSize.uml;
    }
  },
  setEditor: function setEditor(state, editor) {
    state.editor = editor;
  },
  setEditorText: function setEditorText() {
    state.editor.setValue(state.text, 1);
  },
  setText: function setText(state, text) {
    state.text = text;
  },
  renderUML: function renderUML(state, text) {
    state.src = state.server + state.umlExtension + '/' + plantumlEncoder.encode(text);
  },
  setLocalStrage: function setLocalStrage(state, text) {
    if (window.localStorage) {
      window.localStorage.setItem(state.plantuml, text);
    }
  },
  getLocalStrage: function getLocalStrage(state) {
    var text = window.localStorage ? window.localStorage.getItem(state.plantuml) : '';
    state.text = text || state.defaultText;
  },
  setIsLoading: function setIsLoading(state, isLoading) {
    state.isLoading = isLoading;
  },
  download: function download(state) {
    var ext = mutations.getExtFromText();
    axios.get(state.src || '', {
      'responseType': ext.responseType
    }).then(function (response) {
      if (response && response.data) {
        var downLoadLink = document.createElement('a');
        downLoadLink.download = state.plantuml + '.' + state.umlExtension;
        downLoadLink.href = URL.createObjectURL(new Blob([response.data], { type: ext.fileType }));
        downLoadLink.dataset.downloadurl = ext.fileType + ':' + downLoadLink.download + ':' + downLoadLink.href;
        downLoadLink.click();
      }
    });
  },
  getExtFromText: function getExtFromText() {
    return _.find(state.umlExtensions, { 'text': state.umlExtension });
  }
};

var actions = {
  setUmlWidth: function setUmlWidth(context, umlWidth) {
    context.commit('setUmlWidth', umlWidth);
  },
  setUmlExtension: function setUmlExtension(context, umlExtension) {
    context.commit('setUmlExtension', umlExtension);
  },
  setIsLoading: function setIsLoading(context, isLoading) {
    context.commit('setIsLoading', isLoading);
  },
  setColSize: function setColSize(context, colSize) {
    context.commit('setColSize', colSize);
  },
  resetColSize: function resetColSize(context, colSize) {
    context.commit('setColSize', {
      'history': 2,
      'editor': 4,
      'uml': 6
    });
  },
  setEditor: function setEditor(context, editor) {
    context.commit('setEditor', editor);
  },
  setEditorText: function setEditorText(context) {
    context.commit('setEditorText');
  },
  getLocalStrage: function getLocalStrage(context) {
    context.commit('getLocalStrage');
    context.commit('getUmlWidthFromLocalStorage');
  },
  syncText: function syncText(context, text) {
    context.commit('setText', text);
    context.commit('setLocalStrage', text);
  },
  renderUML: function renderUML(context, text) {
    context.commit('setText', text);
    context.commit('renderUML', text);
    context.commit('setLocalStrage', text);
  },
  download: function download(context) {
    context.commit('download');
  }
};

export default {
  state: state,
  mutations: mutations,
  actions: actions
};


